.btn, button {
  font-size: 1.6rem;
  outline: none;
  box-shadow: none;
  border: none;
  line-height: 1;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  padding: 1rem 2rem;
  color: #232323;
  font-weight: 400;
  cursor: pointer;
  border-radius: 8px;
}
.btn.fill-white, button.fill-white {
  background-color: #fff;
  box-shadow: 0px 12px 40px #cdd0d5;
  transition: all 0.2s ease-in-out;
}
.btn.fill-white:hover, button.fill-white:hover {
  transform: scale(1.02);
}
.btn.arrow-right svg, button.arrow-right svg {
  display: inline-block;
  margin-left: 10px;
  width: 7px;
}
.btn.img-right img,
.btn.img-right svg:not(.arrow-svg), button.img-right img,
button.img-right svg:not(.arrow-svg) {
  width: 3rem;
  display: inline-block;
  margin-left: 1rem;
}
.btn.img-left img,
.btn.img-left svg:not(.arrow-svg), button.img-left img,
button.img-left svg:not(.arrow-svg) {
  width: 3rem;
  display: inline-block;
  margin-right: 1rem;
}
.btn.btn-icon, button.btn-icon {
  background-color: inherit;
  padding: 0.8rem;
  box-shadow: none;
  border-radius: 32px;
  border: none;
  line-height: 1;
  display: inline-flex;
  align-items: center;
  font-size: 1.6rem;
  cursor: pointer;
  transition: transform 0.2s ease-out;
  border: 1px solid #F3F3F3;
}
.btn.btn-icon svg,
.btn.btn-icon img, button.btn-icon svg,
button.btn-icon img {
  width: 20px;
}
.btn.btn-icon.text svg, button.btn-icon.text svg {
  margin-right: 3px;
}
.btn.btn-icon:hover, button.btn-icon:hover {
  transform: scale(1.03);
}
.btn.submit-form-btn, button.submit-form-btn {
  border: none;
  background-color: #3757FF;
  color: #fff;
  padding: 1.5rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.2s ease-out;
  width: 100%;
}
.btn.submit-form-btn:hover, button.submit-form-btn:hover {
  transform: scale(1.02);
}
.btn.submit-form-btn:disabled, button.submit-form-btn:disabled {
  cursor: no-drop;
  background-color: #fff;
  border: 1px solid #ECECEF;
  color: #8F92A1;
}
.btn.submit-form-btn:disabled:hover, button.submit-form-btn:disabled:hover {
  transform: scale(1);
}
.btn.submit-form-btn svg, .btn.submit-form-btn img, button.submit-form-btn svg, button.submit-form-btn img {
  margin-right: 1rem;
  width: 10px;
}
.btn.submit-form-btn svg path, .btn.submit-form-btn img path, button.submit-form-btn svg path, button.submit-form-btn img path {
  fill: #fff;
}

label.crop-radio img {
  height: 50px;
  width: 50px;
  object-fit: contain;
  object-position: center;
}
label.crop-radio [type=radio] {
  position: absolute;
  opacity: 0;
}
label.crop-radio [type=radio] + div {
  cursor: pointer;
  padding: 5px 10px;
  border: 1px solid #F9F9FA;
  border-radius: 8px;
}
label.crop-radio [type=radio]:checked + div {
  border: 1px solid #1B84FF;
  background-color: #D1E6FF;
}

label.color-picker-radio {
  width: 30px;
  height: 30px;
  cursor: pointer;
}
label.color-picker-radio [type=radio], label.color-picker-radio [type=color] {
  position: absolute;
  opacity: 0;
}
label.color-picker-radio [type=radio] + div, label.color-picker-radio [type=color] + div {
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  padding: 2px;
  z-index: 1;
}
label.color-picker-radio [type=radio] + div div, label.color-picker-radio [type=color] + div div {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
label.color-picker-radio [type=radio]:checked + div, label.color-picker-radio [type=color]:checked + div {
  border: 3px solid;
}

