a.video-item{
    box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.191);
    width: 100%;
    min-width: 380px;
    border-radius: 8px;
    display: block;
    text-decoration: none;
    transition: transform 0.3s ease-in-out;

    &:hover{
        transform: scale(1.01) translate(5px, 5px);
    }

    //delet
    height: 300px;

    .preview-img{
        width: 100%;
        height: 60%;

        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
        // background-color: mediumaquamarine;
        overflow: hidden;

        img{
            object-fit: cover;
            height: 100%;
            width: 100%;
        }
    }

    .description{
        padding: 2rem;
        height: 40%;
        display: flex;
        flex-direction: column;

        h5{
            color: $black;
        }

        p{
            color: rgb(132, 129, 138);
            margin-top: 1rem;
            margin-bottom: 0;

            &.author{
                margin-top: auto;   
            }
        }
    }
}